import { combineReducers } from 'redux';
import { reducer as formReducer } from 'redux-form';

import newJobReducer from './newJobReducer';
import categoriesReducer from './categoriesReducer';
import toUploadReducer from './toUploadReducer';
import clearStorageReducer from './clearStorageReducer';

const appReducer = combineReducers({
  form: formReducer,
  newJob: newJobReducer,
  categories: categoriesReducer,
  uploadQueue: toUploadReducer,
  clearStorage: clearStorageReducer
})

const rootReducer = (state, action ) => {
  if (action.type === 'CLEAR_LOCALSTORAGE'){
    state = undefined
  }
  return appReducer(state, action)
}

export default rootReducer;
