import React from 'react';
import Modal from '../Modal';
import { Redirect, Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { clearLocalStorage } from '../../actions';
import db from '../../database/db';

class DeleteJob extends React.Component {

  state = { cancel: false, toPath: false }

  //Remove localStorage and indexDB data, redirect to createJob page
  resetData = async () => {
    this.props.clearLocalStorage();
    //console.log('Storage should be cleared: ',localStorage.getItem("state"));
    await db.delete();
    this.setState({ toPath: true });
  }

  renderActions(){
    return(
      <React.Fragment>
        <button onClick={() => this.resetData()} className="ui button negative">New Job</button>
        <Link to="/categories" className="ui button">
          Cancel
        </Link>
      </React.Fragment>
    );
  }

  render(){

    if (this.state.cancel){
      return <Redirect to="/categories"/>
    }
    else if (this.state.toPath){
      return <Redirect to="/"/>
    }

    return (
      <div>
        <Modal
          title='Detected previous session.  Delete old photos?'
          content='You have session data.  If you create a new job, you will lose all the photos you took from your previous session.  Delete photos and create a new job?'
          path='/categories'
          actions={this.renderActions()}
          onDismiss={() => this.setState({ toPath: true })}
        />
      </div>
    );
  }

};

export default connect(null, { clearLocalStorage })(DeleteJob);
