/*
App

Sets up history and routing for components.

*/

import React from 'react';
//Import history.  May have to change this based on Host site
import { HashRouter, Route } from 'react-router-dom';
import { ToastProvider } from 'react-toast-notifications';
//import history from '../history';
//Import local components
import CreateJob from './pages/CreateJob';
import CategoryList from './pages/CategoryList';
import CategoryEdit from './pages/CategoryEdit';
import DeleteJob from './pages/DeleteJob';



const App = () => {

  return (
    <ToastProvider>
      <p>v0.2.0</p>
      <div className="ui container" style={{ marginTop: '10px'}}>
        <HashRouter>
          <div>
            <Route path="/" exact component={CreateJob}/>
            <Route path="/categories" exact component={CategoryList}/>
            <Route path="/categories/:id" exact component={CategoryEdit}/>
            <Route path="/delete" exact component={DeleteJob}/>
          </div>
        </HashRouter>
      </div>
    </ToastProvider>
  );
}

export default App;
